:root{
  --color1:#FD808B;
  --color2:#34A38B;
  --color3:#F8B520;
}
body {
  background-color: #c4ccd5;
}
.gantt_task_content {
  color: #778CA2 !important;
}

.gantt_cell,
.gantt_cell_tree {
  width: 900px;
}
/* .gantt_grid_scale .gantt_grid_head_cell {
  display: none;
} */
/* .gantt_tree_content {
  display: none;
} */
.gantt_tree_icon,
.gantt_file {
  display: none;
}

.gantt_task_row {
  display: none;
}

.gantt_line_wrapper {
  display: none;
}

.gantt_add {
  display: none;
}

.weekend {
  background: #ecfbf8 !important;
  color: #44d7b6 !important;
  font-weight: 800 !important;
}

.gantt_task_progress_drag {
  display: none !important;
}
.greenBorder .gantt_task_progress {
  background-color:var(--color2) !important;
  color: white;
}
.greenBorder {
  border-radius: 5px !important;
  border: var(--color2)  3px solid !important;
  color: white !important;
}
.greenBorder:hover {
  border-left: var(--color2)  10px solid !important;
  border-right: var(--color2)  10px solid !important;
  color: white !important;
}
.greenBorder .gantt_task_content{
  color: white !important;
}
.pinkBorder {

  border-radius: 5px !important;
  border: var(--color1) 3px solid !important;
  background-color: white !important;
}
.pinkBorder:hover {
  border-left: var(--color1) 10px solid !important;
  border-right: var(--color1) 10px solid !important;
}
.pinkBorder .gantt_task_progress {
  position: absolute;
  background-color:var(--color1) !important;
  text-align: end;
  align-items: end;
}
.orangeBorder {
  border-radius: 5px !important;
  border: var(--color3) 3px solid !important;
  background-color: white !important;
}
.material-icons{
 color:black;
 /* font-weight: 800; */

 font-size: none !important;
  display:none;
 line-height: none !important;

}



.gantt_link_control{
  display: none !important;
}
.orangeBorder:hover {
  border-left: var(--color3)  10px solid !important;
  border-right: var(--color3) 10px solid !important;
}
.gantt_tree_content{
  font-weight: 900;}
.orangeBorder .gantt_task_progress {
  position: relative;
  background-color: white !important;
  text-align: right !important;
  align-items: right !important;
  align-content: right !important;
}
.gantt_task_progress {
  background: red;
}
.txtAlign {
  text-align: right;
  /* background-color: yellow; */
}
.vv {
  background-color: black;
}

